import Vue from 'vue';
import Router from 'vue-router';

import Home from './home/Home.vue';
import Calendar from './calendar/Calendar.vue';
import Login from './login/Login.vue';
import Signup from './login/Signup.vue';

Vue.use(Router);


// Auth guard - simply checks if the user logged in.
import Store from './store';
function CheckUser(to: any, from: any, next: any): any{
  if (Store.getters.getUser !== null) { // If logged in, allow them to continue to the requested route
    next();
  }
  else { // Otherwise, redirect to signin
    Store.dispatch('setError', {message: 'You must be signed in to access this page.', type: 'login'});
    next('/login');
  }
}

// Configure routes
const routes = [
  { path: '/', component: Home, props: { name: 'Vue' }},
  { path: '/login', component: Login },
  { path: '/signup', component: Signup },
  { path: '/calendar', component: Calendar, beforeEnter: CheckUser},
  { path: '*', redirect: '/' },
];

export default new Router ({
    mode: 'history',
    linkActiveClass: 'active',
    routes,
});
