



















































































































import Vue from 'vue';
import Axios from 'axios';
import { Component } from 'vue-property-decorator';

@Component
export default class AppComponent extends Vue {
    public clipped = false;
    public drawer = true;
    public fixed = false;

    public miniVariant = true;
    public right = true;
    public title = 'ORGnizer';
    public name = '';

    public fav = false;
    public hints = false;
    public message = false;

    // List of menu items to be displayed
    get menuItems() {

        const menuItems = [{
                icon: 'home',
                title: 'Home',
                to: '/',
                type: 'button',
            },
            {
                icon: 'calendar_today',
                title: 'Calendar',
                to: '/Calendar',
                type: 'button',
            },
        ];

        if (!this.userIsAuthenticated) {
            menuItems.push({
                icon: 'lock_open',
                title: 'Login',
                to: '/login',
                type: 'button',
            });
        } else {
            menuItems.push({
                icon: 'account_circle',
                title: 'Account',
                to: '',
                type: 'dropdown',
            });
        }

        return menuItems;
    }

    // Checks that the user is current signed in
    get userIsAuthenticated() {
        return (this.$store.getters.getUser !== null && this.$store.getters.getUser !== undefined);
    }

    get getUserName() {
        return 'John Dorian';
    }

    // Log current user out
    private logOut() {
      this.$store.dispatch('signUserOut');
    }


}
