import Vue from 'vue';
import Vuex from 'vuex';
import Auth from 'firebase';
import Axios from 'axios';
import { ServerEventUser } from '@servicestack/client';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        user: null,
        loading: false,
        error: null,
    },
    actions: {
        signUserIn({ commit }, payload) {
            commit('setLoading', true);
            commit('setError', null);
            Auth.auth().signInWithEmailAndPassword(payload.email, payload.password)
                .then(
                    (response) => {
                        commit('setLoading', false);
                        const newUser = {
                            id: response.user == null ? '' : response.user.uid,
                            first: '',
                            last: '',
                            registeredEvents: [],
                        };
                        commit('setUser', newUser);
                    })
                .catch(
                    (error) => {
                        commit('setLoading', false);
                        commit('setError', {message: error.message, type: 'login'});
                    },
                );
        },
        signUserOut({ commit }) {
            commit('setUser', null);
        },
        signUserUp({ commit }, payload) {
            commit('setLoading', true);
            commit('setError', null);
            Auth.auth().createUserWithEmailAndPassword(payload.email, payload.password)
                .then(
                    (response) => {
                        commit('setLoading', false);
                        const newUser = {
                            id: response.user == null ? '' : response.user.uid,
                            first: '',
                            last: '',
                            registeredEvents: [],
                        };
                        commit('setUser', newUser);
                    })
                .catch(
                    (error) => {
                        commit('setLoading', false);
                        commit('setError', {message: error.message, type: 'login'});
                    },
                );
        },
        authSignIn( {commit}, payload) {
            if (store.getters.getError && store.getters.getError.type === 'login') {
                commit('setError', null);
            }
            commit('setUser', {id: payload.uid});
        },
        setError({commit}, payload) {
            commit('setError', payload);
        },
        setLoading({commit}, payload) {
            commit('setLoading', payload);
        },
    },
    mutations: {
        setUser(state, payload) {
            state.user = payload;
            if (payload == null) {
                Auth.auth().signOut();
            }
        },
        setLoading(state, payload) {
            state.loading = payload;
        },
        setError(state, payload) {
            state.error = payload;
        },
    },
    getters: {
        getUser(state) {
            return state.user;
        },
        getError(state) {
            return state.error;
        },
        getLoading(state) {
            return state.loading;
        },
    },
    modules: {

    },
});

export default store;
