<template> 
    <app-ds-calendar-app ref="app"
      :calendar="calendar"
      :read-only="readOnly"
      @change="saveState">

      <template slot="error">
        <app-alert v-if="error" @dismissed="onDismissed" :text="error.message" type="info"></app-alert>
      </template>

      <template slot="title">
        Current Calendar
        <v-btn color="info" flat disabled :loading="loading"></v-btn>
      </template>

      <!-- <template slot="menuRight">
        <v-btn icon large href="https://github.com/ClickerMonkey/dayspan-vuetify" target="_blank">
          <v-avatar size="32px" tile>
            <img src="https://simpleicons.org/icons/github.svg" alt="Github">
          </v-avatar>
        </v-btn>
      </template> -->

      <template slot="eventPopover" slot-scope="slotData">
         <ds-calendar-event-popover
          v-bind="slotData"
          :read-only="readOnly"
          @finish="saveState"
        ></ds-calendar-event-popover>
      </template>

      <template slot="eventCreatePopover" slot-scope="{placeholder, calendar, close}">
        <ds-calendar-event-create-popover
          :calendar-event="placeholder"
          :calendar="calendar"
          :close="$refs.app.$refs.calendar.clearPlaceholder"
          @create-edit="$refs.app.editPlaceholder"
          @create-popover-closed="saveState"
        ></ds-calendar-event-create-popover>
      </template>

      <template slot="eventTimeTitle" slot-scope="{calendarEvent, details}">
        <div>
          <v-icon class="ds-ev-icon"
            v-if="details.icon"
            size="14"
            :style="{color: details.forecolor}">
            {{ details.icon }}
          </v-icon>
          <strong class="ds-ev-title">{{ details.title }}</strong>
        </div>
        <div class="ds-ev-description">{{ getCalendarTime( calendarEvent ) }}</div>
      </template>

      <template slot="drawerBottom">
        <div class="pa-3">
          <v-checkbox
            label="Read Only?"
            v-model="readOnly"
          ></v-checkbox>
        </div>
      </template>

    </app-ds-calendar-app>

</template>

<script>
import { Calendar, Weekday, Month } from 'dayspan';
import Firebase from 'firebase';
import Vue from 'vue';
export default {
  name: 'app',
  data: () => ({
    storeKey: 'dayspanState',
    calendar: Calendar.months(),
    readOnly: false,
    defaultEvents:       [{
        data: {
          title: 'Weekly Meeting',
          color: '#3F51B5',
        },
        schedule: {
          dayOfWeek: [Weekday.MONDAY],
          times: [9],
          duration: 30,
          durationUnit: 'minutes',
        },
      }],
  }),
  mounted()
  {
    window.app = this.$refs.app;
    this.loadState();
    var calendarRef = Firebase.database().ref('user/' + this.$store.getters.getUser.id + '/calendarData');
      calendarRef.on('value', (data) => {
        const state = JSON.parse(data.val());
        if(state)
        {
          this.$refs.app.setState( state );
        }
      });
  },
  methods:
  {
    onDismissed() {
      this.$store.dispatch('setError', null);
    },
    getCalendarTime(calendarEvent)
    {
      const sa = calendarEvent.start.format('a');
      const ea = calendarEvent.end.format('a');
      let sh = calendarEvent.start.format('h');
      let eh = calendarEvent.end.format('h');
      if (calendarEvent.start.minute !== 0)
      {
        sh += calendarEvent.start.format(':mm');
      }
      if (calendarEvent.end.minute !== 0)
      {
        eh += calendarEvent.end.format(':mm');
      }
      return (sa === ea) ? (sh + ' - ' + eh + ea) : (sh + sa + ' - ' + eh + ea);
    },
    saveState()
    {
      const state = this.calendar.toInput(true);
      const json = JSON.stringify(state);
      this.$store.dispatch('setLoading', true);
      Firebase.database().ref('user/' + this.$store.getters.getUser.id + '/calendarData').set(json)
      .then((x) => {
        this.$store.dispatch('setLoading', false);
      });
      // localStorage.setItem(this.storeKey, json);
    },
    loadState()
    {
      let state = {};
      try
      {
        this.$store.dispatch('setLoading', true);
        Firebase.database().ref('user/' + this.$store.getters.getUser.id + '/calendarData').once('value')
        .then((data) => {
          const values = JSON.parse(data.val());
          if (values) {
            state = values;
            state.preferToday = false;
          }
          if (!state.events || !state.events.length)
          {
            // state.events = this.defaultEvents;
            this.$store.dispatch('setError', {message: 'No events could be loaded. Either no events are currently present on your calendar or an error occurred.', type: 'calendar'});
          }
          else
          {
            this.$refs.app.setState( state );
          }

          this.$store.dispatch('setLoading', false);
        })
        .catch((error) => {
          console.log(error);
        });

        // const savedState = values;
        /* Local save */
        // const savedState = JSON.parse(localStorage.getItem(this.storeKey));
        // if (savedState)
        // {
        //   state = savedState;
        //   console.log('loadedFromLocalJSON');
        //   console.log(state);
        //   state.preferToday = false;
        // }
      }
      catch (e)
      {
        // eslint-disable-next-line
        // console.log( e );
      }
      /* Local save */
      // if (!state.events || !state.events.length)
      // {
      //   state.events = this.defaultEvents;
      //   console.log('Error Ocurred while loading. Resetting to default events');
      // }
      /* Load default events */
      // state.events.forEach((ev) =>
      // {
      //   // console.log(ev);
      //   const defaults = this.$dayspan.getDefaultEventDetails();
      //   ev.data = Vue.util.extend( defaults, ev.data );
      // });
      // this.$refs.app.setState( state );
    },
  },
  computed: {
    loading() {
      return this.$store.getters.getLoading;
    },
    error() {
      return this.$store.getters.getError;
    },
  },
};
</script>

<style>
body, html, #app, #dayspan {
  font-family: Roboto, sans-serif !important;
  width: 100%;
  height: 100%;
}
.v-btn--flat,
.v-text-field--solo .v-input__slot {
  background-color: #f5f5f5 !important;
  margin-bottom: 8px !important;
}
</style>